import { createRouter, createWebHistory } from 'vue-router'
import HomeView from '../views/HomeView.vue'
import PreventDefaultPassWord from '../views/PreventDefaultPassWord.vue'
import AdminiView from '../views/AdminiView.vue'
import CalendarChart from '../views/CalendarChart.vue'
import ThankView from '../views/ThankView.vue'

const routes = [
  {
    path: '/',
    name: 'home',
    component: HomeView
  },
  {
    path: '/pass',
    name: 'pass',
    component: PreventDefaultPassWord
  },
  {
    path: '/admini',
    name: 'admini',
    component: AdminiView
  },
  {
    path: '/calendarchart',
    name: 'calendarchart',
    component: CalendarChart
  },
  {
    path: '/thank',
    name: 'thank',
    component: ThankView
  }
]

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes
})

export default router
